.sticky {
	display: block;
}

.hentry {
	margin: 0 0 1.5em;
}
.entry-header {
	font-family: $font_sans;
}

.cat-links {
	font-size: 90%;
	font-weight: 700;

	a {
		 text-decoration: none;
		  text-transform: uppercase;
			color: $color_interactive;


			&:focus,
			  &:hover {
				  background-color: $color_interactive;
					color: white;
			  }
	}
}

.entry-title {
	margin: .125em 0 .25em;
	font-size: 2.5em;
	  
	@media screen and (min-width: $query_medium){
		font-size: 3em;
	}

	a {
		text-decoration: none;
		color: black;

		&:focus,
		   &:hover {
			   border-bottom: 5px solid $color_interactive;
		   }
	}
}


.entry-meta {
	font-size: 90%;


	 a {
		 font-weight: 700;
		  text-decoration: none;
			color:black;

		  &:focus,
			&:hover {
				color:black;
				border-bottom: 3px solid $color_interactive;
			}
	 }
}

.byline {
	 &::after {
		 content: "|" ;
		  margin: 0 .5em;
	 }
}

.comments-link,
.edit-link {
	 
	&::before {
		  content: "|";
		  margin: 0 .5em;
	}
}

.byline,
.updated:not(.published){
	display: none;
}

.single .byline,
.group-blog .byline {
	display: inline;
}

.extra {
	display: none;
}
/* Custom Post Meta rules for single post / no sidebar. */

.no-sidebar {

	.entry-meta {
		margin-top: 1.5em;
	}

	@media screen and (min-width: $query_medium) {
		.post-content__wrap {

			.entry-meta {
				margin-top: 2.25em;
				font-size: 80%;

				a {
					font-size: 110%;
					display: block;
					border-bottom: 3px solid white;

					&:hover,
					&:focus {
						border-bottom-color: $color_interactive;
					}
				}
			}

			.byline,
			.posted-on,
			.comments-link,
			.edit-link {
				display: block;
				margin-bottom: 1em;

				&:before,
				&:after {
					margin: 0;
					content: "";
				}
			}

			.extra {
				display: inline;
			}
		}

	}
}

.page-content,
.entry-content,
.entry-summary {
	margin: 1.5em 0 0;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}
